*{
    box-sizing: border-box;
}

body {
    font-family: "inter";
    margin: 0;
    height: 100vh;
    background-color: #282D35;

}

nav {
    display: flex;
    align-items: center;
    background-color: #21222A;
    height: 90px;
    padding: 30px 25px;
}

.navbar-logo {
    width: 65px;
    margin-right: 7px;
}

.navbar-logo-text {
    margin: 0;
    margin-right: auto;

    color: #61DAFB;
    font-size: 22px;
}

.navbar-title {
    margin: 0;
    color: #DEEBF8;
    font-weight: 600;
}

main {
    padding: 57px 27px;
    color: white;
}

.main-title {
    margin: 0;
    font-size: 39px;
    letter-spacing: -0.05em;
}

.main-facts {
    margin-top: 46px;
}

.main-facts > li {
    line-height: 19px;
    padding-block: 10px;
    max-width: 400px;
}

.main-facts > li::marker {
    font-size: 1.4rem;
    color: #61DAFB;
}